<template>
  <div id="app">
    <div id="starshine">
      <svg class="circle" viewBox="0 0 100 100">
      <path id="circle" d="M 0,50 a 50,50 0 1,1 0,1 z" />
        <text>
          <textPath xlink:href="#circle">
            ЭТО ТУСА ЭТО ТУСА
          </textPath>
        </text>
      </svg>
      <MenuComponent />
      <div class="template shine"></div>
    </div>
    <div id="discoBallLight"></div>
    <div id="discoBall">
      <div id="discoBallMiddle"></div>
    </div>
  </div>
</template>

<script>
import MenuComponent from "./components/MenuComponent.vue";

export default {
  name: "App",
  components: {
    MenuComponent,
  },
  mounted() {
    var radius = 50;
    var squareSize = 6.5;
    var prec = 23.55;
    var fuzzy = 0.001;
    var inc = (Math.PI - fuzzy) / prec;
    var discoBall = document.getElementById("discoBall");

    for (var t = fuzzy; t < Math.PI; t += inc) {
      var z = radius * Math.cos(t);
      var currentRadius =
        Math.abs(
          radius * Math.cos(0) * Math.sin(t) -
            radius * Math.cos(Math.PI) * Math.sin(t)
        ) / 2.5;
      var circumference = Math.abs(2 * Math.PI * currentRadius);
      var squaresThatFit = Math.floor(circumference / squareSize);
      var angleInc = (Math.PI * 2 - fuzzy) / squaresThatFit;
      for (var i = angleInc / 2 + fuzzy; i < Math.PI * 2; i += angleInc) {
        var square = document.createElement("div");
        var squareTile = document.createElement("div");
        squareTile.style.width = squareSize + "px";
        squareTile.style.height = squareSize + "px";
        squareTile.style.transformOrigin = "0 0 0";
        squareTile.style.webkitTransformOrigin = "0 0 0";
        squareTile.style.webkitTransform =
          "rotate(" + i + "rad) rotateY(" + t + "rad)";
        squareTile.style.transform =
          "rotate(" + i + "rad) rotateY(" + t + "rad)";
        if ((t > 1.3 && t < 1.9) || (t < -1.3 && t > -1.9)) {
          squareTile.style.backgroundColor = randomColor("bright");
        } else {
          squareTile.style.backgroundColor = randomColor("any");
        }
        square.appendChild(squareTile);
        square.className = "square";
        squareTile.style.webkitAnimation = "reflect 2s linear infinite";
        squareTile.style.webkitAnimationDelay =
          String(randomNumber(0, 20) / 10) + "s";
        squareTile.style.animation = "reflect 2s linear infinite";
        squareTile.style.animationDelay =
          String(randomNumber(0, 20) / 10) + "s";
        squareTile.style.backfaceVisibility = "hidden";
        var x = radius * Math.cos(i) * Math.sin(t);
        var y = radius * Math.sin(i) * Math.sin(t);
        square.style.webkitTransform =
          "translateX(" +
          Math.ceil(x) +
          "px) translateY(" +
          y +
          "px) translateZ(" +
          z +
          "px)";
        square.style.transform =
          "translateX(" +
          x +
          "px) translateY(" +
          y +
          "px) translateZ(" +
          z +
          "px)";
        discoBall.appendChild(square);
      }
    }

    function randomColor(type) {
      var c;
      if (type == "bright") {
        c = randomNumber(130, 255);
      } else {
        c = randomNumber(110, 190);
      }
      return "rgb(" + c + "," + c + "," + c + ")";
    }

    function randomNumber(min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min;
    }
    const body = document.getElementById("starshine");
    const template = document.querySelector(".template.shine");
    const stars = 150;
    const sparkle = 20;
    let size = "small";

    function createStar() {
      const clone = template.cloneNode(true); // true для глубокого клонирования
      clone.removeAttribute("id");
      clone.style.top = Math.random() * 210 + "%";
      clone.style.left = Math.random() * 90 + "%";
      clone.style.webkitAnimationDelay = Math.random() * sparkle + "s";
      clone.style.mozAnimationDelay = Math.random() * sparkle + "s";
      clone.classList.add(size);
      body.appendChild(clone);
    }

    for (let i = 0; i < stars; i++) {
      if (i % 2 === 0) {
        size = "small";
      } else if (i % 3 === 0) {
        size = "medium";
      } else {
        size = "large";
      }

      createStar();
    }
  },
};
</script>

